* {
  scroll-behavior: smooth;
}

html {
  background-color: whitesmoke;
}

.app-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
  border: 10px white solid;
}

.content-container {
  width: 100%;
  overflow: scroll;
  display: flex;
  flex-direction: column;
}

.section-title {
  margin-bottom: 0.75em;
  font-size: 2.5em;
  font-family: "Poppins", sans-serif;
  color: rgb(0, 0, 0);
  text-decoration: underline;
}

/* ---- Misc ---- */

.highlight {
  color: rgb(15, 209, 135);
}

#scroll-down-icon {
  width: 3em;
  position: absolute;
  z-index: 3;
  
}

.curved {
  display: flex;
  flex-direction: column;
  margin-bottom: -2em;
}

.svg-top {
  margin-bottom: -1px;
}
