.sidebar {
  background-color: rgb(25, 26, 31);
  height: 100%;
  width: 15em;
  display: flex;
  justify-content: center;
  z-index: 2;
  transition: all .3s;
}

.sidebar-hide {
  width: 0em;
  height: 100%;
  position: absolute;
  transition: all .3s;
}

.sidebar-hide a {
  display: none;
}

.nav-links {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0 auto;
  padding: 0.75em;
  font-size: 27px;
  list-style-type: none;
  transition: all .6s;
}

.list-item {
  padding-bottom: 0.25em;
}

.list-item {
  float: left;
}

.nav-item {
  color: rgb(255, 255, 255);
  text-decoration: none;
  transition: 0.3s;
  font-family: "Josefin Sans", sans-serif;
  display: flex;
  align-items: center;
}

.nav-item:hover {
  color: rgb(15, 209, 135);
  padding-left: 10px;
}


#social-container {
  font-family: "Josefin Sans", sans-serif;
  font-size: 20px;
  color: black;
  align-self: flex-start;
}

.sidebar-icon {
  width: 30px;
  padding-right: 10px;
}

#hamburger-toggle {
  font-size: 38px;
  color: black;
  position: absolute;
  top: 0;
  left: 0;
  margin: 20px 0em 0em 20px;
  z-index: 1;
  transition: 0.3s;
  cursor: pointer;
}

#hamburger-toggle:hover {
  color: rgb(15, 209, 135);
}

#sidebar-exit {
  margin-bottom: 20px;
  color: rgb(255, 255, 255);
  transition: 0.3s;
  cursor: pointer;
}

#sidebar-exit:hover {
  color: red;
}