#skills-container {
  width: 60%;
	margin: 2em auto 2em;
}

#skills-section {
  padding: 2em;
  margin: auto;
  text-align: left;
  font-family: "Josefin Sans", sans-serif;
  width: 100%;
  background-color: rgb(25, 26, 31);
  border-radius: 0.625em;
  display: relative;
}

.skill-tiles-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 150px;
  grid-gap: 20px;
}

.skill-tile {
  background-color: rgb(25, 26, 31);
  border-radius: 10px;
	display: flex;
	justify-content: center;
  align-items: center;
}

.skill-tile i {
	font-size: 80px;
}